import Actions from "../store/actions.types";
export default {
  data() {
    return {
      user: {
        name: "",
        password: "",
        email: "",
        confirmPassword: "",
        address: {
          street: "",
          city: "",
          zip: "",
        },
      },
      localError: null,
    };
  },
  methods: {
    submit() {
      if (this.user.password !== this.user.confirmPassword) {
        this.$store.dispatch(Actions.SET_RESPONSE, {
          type: "error",
          message: "passwords don't match",
        });
        return;
      }
      this.$emit("submitted", this.user);
    },
  },
};
