import Product from "../components/SingleProduct.vue";
import Category from "../components/Category.vue";
import Actions from "../store/actions.types"
export default {
  name: "Home",
  components: { Product, Category },
  data() {
    return {
      categories: [],
      showCategory: 'skateboard'
    };
